.App {
  text-align: center;
}
.nav-app{
  text-align: center;
  padding:2% ;
  box-sizing:content-box;
}
@media (max-width: 500px){
  .nav-app{
    display: grid;
  grid-template-columns: repeat(3, auto);
  }
}
.nav-app .nav-app-link {
  border: 1px solid #000;
  border-radius: 5px;
  margin: 0 1%;
  padding:.75% 4%;
  color: #000;
  text-decoration: none;
  box-sizing:content-box;
}
.selected{
  background-color: #3f51b5;
  border: 1px solid #000;
  border-radius: 5px;
  
}
